<template>
  <div id="quest-list">
    <div class="enter">
      <div class="button" @click="createQuest">新增題目</div>
      <draggable v-model="theQuestArray" :options="{ghostClass: 'ghost', group: 'item'}">
        <transition-group name="item-complete">
          <div class="item" v-for="(quest, index) in theQuestArray" :key="'quest'+index">
            <label>
              <p>題號：</p>
              <div class="input">
                <input type="text" v-model="quest.no">
              </div>
            </label>
            <label>
              <p>問題：</p>
              <div class="input">
                <input type="text" v-model="quest.q">
              </div>
            </label>
            <label>
              <p>圖片網址：</p>
              <div class="input">
                <input type="text" v-model="quest.i">
              </div>
            </label>
            <label class="options">
              <p>選項：</p>
              <div class="input">
                <input type="text" v-for="o in quest.o" :key="o.value" v-model="o.label">
              </div>
            </label>
            <label>
              <p>答案：</p>
              <div class="input">
                <select v-model="quest.a">
                  <option v-for="q in quest.o" :key="q.value" :value="q.value">{{ q.label }}</option>
                </select>
              </div>
            </label>
            <div class="button" @click="deleteQuest(index)">刪除 {{ quest.no }}</div>
          </div>
        </transition-group>
      </draggable>
    </div>
    <div class="result">
      <input id="upload-file-btn" type="file" style="display: none;" @change="upload">
      <div class="btn upload" @click="clickUpload">上傳</div>
      <div class="btn dowmload" @click="saveJSON(theQuestArray, 'quest.json')">下載</div>
      <textarea :value="theQuestArrayString" @input="inputQuestArray($event.target.value)"></textarea>
    </div>
  </div>
</template>
<script>
import draggable from 'vuedraggable'
export default {
  props: ['allQuestArray'],
  data () {
    return {
      theQuestArray: []
    }
  },
  computed: {
    theQuestArrayString () {
      return JSON.stringify(this.theQuestArray, undefined, 2)
    }
  },
  created () {
    console.log(this.allQuestArray)
    this.theQuestArray = this.allQuestArray
  },
  methods: {
    /** 新增題目 */
    createQuest () {
      let newObj = {
        no: '',
        q: '',
        i: '',
        o: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          }
        ],
        a: 0
      }
      this.theQuestArray.unshift(newObj)
    },
    inputQuestArray (val) {
      this.theQuestArray = JSON.parse(val)
      console.log(val)
    },
    saveJSON (data, filename) {
      if (!data) {
        console.error('No data')
        return
      }
      if (!filename) filename = 'console.json'
      if (typeof data === 'object') {
        data = JSON.stringify(data, undefined, 4)
      }
      let blob = new Blob([data], { type: 'text/json' })
      let e = document.createEvent('MouseEvents')
      let a = document.createElement('a')
      a.download = filename
      a.href = window.URL.createObjectURL(blob)
      a.dataset.downloadurl = ['text/json', a.download, a.href].join(':')
      e.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null)
      a.dispatchEvent(e)
    },
    clickUpload () {
      document.querySelector('#upload-file-btn').click()
    },
    upload (event) {
      let reader = new FileReader()
      reader.onload = this.onReaderLoad
      reader.readAsText(event.target.files[0])
    },
    onReaderLoad (event) {
      let obj = JSON.parse(event.target.result)
      this.theQuestArray = obj
    },
    deleteQuest (index) {
      this.theQuestArray.splice(index, 1)
    }
  },
  components: {
    draggable
  }
}
</script>
